import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Diagrama_helpdesk } from '../../../modelo/Diagrama_helpdesk'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'diagrama_helpdesk-dados-edit',   templateUrl: 'diagrama_helpdesk_dadosEdit.component.html' }) export class Diagrama_helpdeskDadosEditComponent implements OnInit {    diagrama_helpdesk: Diagrama_helpdesk;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.diagrama_helpdesk.Diagrama_helpdesk";                 let id = +params['id'];         this.diagrama_helpdesk = new Diagrama_helpdesk();         this.diagrama_helpdesk.cd_diagrama_helpdesk = id;         o.objetoJson = this.diagrama_helpdesk;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a diagrama_helpdesk. ';       }      this.diagrama_helpdesk = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


